.getPhotoUpload {
    width: auto;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    cursor: pointer;
}

.getPhotoUpload>* {
    margin: 7px;
}


.getPhotoCard {
    position: relative;
    width: 112px;
    min-width: 112px;
    max-width: 112px;
    height: 130px;
    min-height: 130px;
    max-height: 130px;
    background: no-repeat center top 24px url(./img/camPH.svg), #E9E9E9;
    border-radius: 1px;
    overflow: hidden;

}

.getPhotoCard img {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    min-height: 100%;
    min-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    overflow: hidden;
    border-radius: 1px;
    transition: all 350ms ease-in-out;
}

.getPhotoRotate {
    display: block;
    position: absolute;
    z-index: 7;
    top: 0;
    left: 0;
    width: 24px;
    height: 24px;
    background: no-repeat center center url(./img/rotate.svg);
    transition: all 350ms ease-in-out;
}

.getPhotoRotate:hover {
    transition: all 350ms ease-in-out;
    transform: rotate(90deg);
    cursor: pointer;
}

.getPhotoDelete {
    display: block;
    position: absolute;
    z-index: 7;
    top: 0;
    right: 0;
    width: 24px;
    height: 24px;
    background: no-repeat center center url(./img/close.svg);
    transition: all 350ms ease-in-out;
}

.getPhotoDelete:hover {
    transition: all 350ms ease-in-out;
    transform: scale(0.8);
    cursor: pointer;
}

.getPhotoMain {
    display: block;
    width: 100%;
    padding: 3px 0;
    position: absolute;
    z-index: 9;
    bottom: 0;
    text-align: center;
    background: rgba(44, 44, 44, 0.37);
    border-radius: 1px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
}

.getPhotoMakeMainWrapper {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 5;
    opacity: 0;
    transition: all 350ms ease-in-out;
}

.getPhotoMakeMain {
    display: block;
    width: 100%;
    padding: 3px 0;
    position: absolute;
    z-index: 4;
    bottom: 0;
    text-align: center;
    background: rgba(44, 44, 44, 0.37);
    border-radius: 1px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
}

.getPhotoMakeMainWrapper:hover {
    transition: all 350ms ease-in-out;
    opacity: 1;
}

.getPhotoNotif {
    position: absolute;
    z-index: 2;
    top: 78px;
    color: #c7c7c7;
    font-size: 12px;
    text-align: center;
}

.file-input {
    display: none;
}

